@import 'shared';

#cards {
  display: flex;
  justify-content: center;
}

.card {
  @include main-shadow;
  display: grid;
  grid-template-columns: 1fr auto;
  padding: 20px;
  width: 150px;
  border-radius: 25px;
  text-align: start;
  row-gap: 10px;
  align-items: center;
  margin: auto 10px;

  .title {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.9em;
    grid-column: 1 / 3;
  }

  .subtitle {
    color: white;
    font-size: 1.2em;
  }
}

.card-buttons {
  display: flex;
  flex-direction: column;

  .card-button {
    color: white;
    font-size: 1.2em;
    cursor: pointer;
  }
}

@media (max-width: 430px) {
  #cards {
    flex-direction: column;
  }

  .card {
    margin: 10px auto;
  }
}