#clock-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

#clock {
  margin: 10px;
  font-variant-numeric: tabular-nums;
}

#time-left {
  position: absolute;
  z-index: 1000;
  color: white;
  font-variant-numeric: tabular-nums;
  font-family: Segment, monospace;
  font-variant-numeric: tabular-nums;
  font-size: 2.8em;
  text-shadow: 0 0 5px rgb(85, 176, 237);
}

#timer-label {
  position: absolute;
  z-index: 1000;
  color: white;
  font-variant-numeric: tabular-nums;
  font-family: monospace;
  font-variant-numeric: tabular-nums;
  font-size: 1em;
  text-shadow: 0 0 5px rgb(85, 176, 237);
  padding-top: 30%;
}

#pointer {
  position: absolute;
}

#beep {
  z-index: 20000;
}
